        border-bottom: 1px solid #eeeeee;
        .container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .header__left {
                flex-basis: 20%;
                width: 20%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                &__logo {
                    img {
                        width: 200px;
                    }
                }
            }
            .header__extra {
                flex-basis: 80%;
                width: 80%;
                display: flex;
                align-items: center;
                .header__catalog-btn {
                    button {
                        font-size: 16px;
                        padding: 10px 20px;
                        display: flex;
                        align-items: center;
                        color: #fff;
                        i{
                            &::before{
                                width: 40px;
                            }
                            margin-right: 10px;
                        }
