                            background-color: lighten(#008dff, 5%);
                        }
                    }
                }
                .header__mid {
                    flex-basis: 90%;
                    width: 90%;
                    padding: 0px 10px;
                    display: flex;
                    position: relative;
                    input {
                        flex-basis: 90%;
                        width: 90%;
                        height: 100%;
                        padding: 11px;
                        transition: .2s ease-in-out;
                        border-right: 0;
                        border-top-right-radius: 0;
                        border-bottom-right-radius: 0;
                    }
                    button {
                        flex-basis: 10%;
