                        border-bottom-left-radius: 0;
                        .fa-magnifying-glass{
                            color: #fff;    
                        }
                        &:hover {
                            background-color: lighten(#008dff, 5%);
                        }
                    }
                    .foundItems{
                        position: absolute;
                        background: #fff;
                        top: 100%; left: 0;
                        margin: 0px 10px;
                        padding: 10px;
                        width: -webkit-fill-available; max-height: 300px;
                        box-shadow: 0px 4px 12px 1px #78787847;
                        border-radius: 6px;
                        display: flex; flex-direction: column;
                        z-index: 10;
                        &__item{
                            margin-bottom: 10px;
                        }
                    }
                }
                .header__right {
                    flex-basis: 10%;
                    width: 10%;
                    height: 100%;
                    display: flex;
                    justify-content: space-around;
                    &__btn {
                        flex-basis: 20%;
                        width: 20%;
                        padding: 10px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;
                        background: 0;
                        span {
                            position: absolute;
                            background-color: var(--main-color);
                            color: #fff;
                            font-size: 14px;
                            width: 20px;
                            height: 20px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 50%;
                            top: -10%;
                            right: -60%;
                        }
                        &.call {
                            display: none;
                        }
                    }
                    
                }
            }
            .header__catalog {
                padding: 0;
                position: absolute; 
                width: 100%; max-height: 0;
                overflow: hidden;
                background-color: #fff;
                top: 74px; left: 0;
                transition: ease .3s;
                border-radius: 6px;
                box-shadow: 0px 20px 20px 0px #1a1a1a13;
                z-index: 9;
                .container{
                    display: flex;
                    align-items: flex-start; 
                    justify-content: flex-start;
                    height: 100%;
                    .sidemenu__left {
                        display: flex;
                        flex-direction: column;
                        height: 100%;
                        width: 32%;
                        padding-bottom: 50px;
                        &__item {
                            width: 100%;
                            display: flex;
                            a{
                                padding: 10px;
                                width: 100%;
                                transition: .1s ease-in-out ;
                                &.show{
                                    background-color: var(--main-color);
                                    color: #fff;
                                }
                            }
                        }
                    }
                    .sidemenu__right {
                        flex-basis: 68%;
                        width: 68%;
