    }

    @media (max-width: 475px) {
        .header {
            .container {
                .header__left {
                    justify-content: center;
                    width: 100%;
                    flex-basis: 100%;
                    &__logo {
                        img {
                            image-rendering: -webkit-optimize-contrast;
                        }
                    }
                    &__btn {
