                    width: 100%;
                    background-color: #fff;
                    padding: 0;
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    z-index: 5;
                    // left-shadow: 0px 16px 20px 8px #565656;
                    .header__mid {
                        display: none;
                    }
                    .header__catalog-btn {
                        flex-basis: 25%;
                        width: 25%;
                        background: 0;
                        padding: 0;
                        button {
                            display: block;
                            width: 100%;
                            padding: 15px 0;
                            background: 0;
                            &:hover {
                                background-color: unset;
                            }
                            .fa-bars {
                                color: #000;
                                margin: 0;
                            }
                            p {
                                display: none;
                            }
                        }
                    }
                    .header__right {
                        flex-basis: 75%;
                        width: 75%;

                        &__btn {
                            flex-basis: 33%;
                            width: 33%;
                            padding: 15px 0px;
                            margin: -15px;

                            &.call {
                                display: flex;
                            }

                            span {
                                font-size: 10px;
                                width: 15px;
                                height: 15px;
                                top: 15%;
                                right: 30%;
                            }
                        }
                    }
                }
                .header__catalog{
                    z-index: 2;
                    top: 60px;
                    left: 0;
                    height: 100vh;
                    width: 0;
                    white-space: pre;
                    &.show{
                        position: fixed;
                        left: 0;
                        width: 100%;
                        height: 100vh;
                    }
                    .container{
                        .sidemenu__left{
                            width: 100%;
                            z-index: 1;
                            padding: 60px 0;
                        }
                        .sidemenu__right{
                            position: absolute;
                            top: 0;
                            left: -100vw;
                            z-index: 2;
                            transition: .3s ease;
