<template>
    <div  class="product-item" v-for="(item, index) of items" :key="index">
        <router-link class="product-item__top" :to="`/product/${item._id}`">
            <img :src="'https://wt.altech.uz/'+item.img" alt="">
        </router-link>
        <div class="product-item__bottom">
            <div class="product-item__bottom__name">{{item.title}}</div>
            <div class="product-item__bottom__info">{{item.descriptions}}</div>
            <div class="product-item__bottom__more">
                <router-link :to="`/product/${item._id}`" class="product-item__bottom__more__price">{{item.price.toLocaleString()}} сум</router-link>
                <div class="product-item__bottom__more__cart">
                    <button @click="addCart(item)">
                        <!-- <fa icon="faCartShopping"/> -->
                        <img src="@/assets/img/icons/cart.svg" alt="">
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['items'],
    data(){
        return{
            
        }
    },
    methods: {
        addCart(item){
            let products = []
            if (this.$cookies.isKey('cart'))
                products = JSON.parse(this.$cookies.get('cart'))
            let index = products.findIndex(product => product.pro._id == item._id)
            let pro = {
                title: item.title,
                price: item.price,
                img: item.img,
                _id: item._id,
            }
            if (index !== -1) {
                products[index].count += 1
            }
            else {
                products.push({pro,count:1})
            }
            this.$store.dispatch('setCartProducts',products)
            this.$cookies.set('cart',JSON.stringify(products))
        }
    }
}
</script>

<style lang="less" scoped>
.product-item{
    display: flex; flex-direction: column;
    border-radius: 6px;
    padding: 10px;
    transition: .3s ease-in-out;
    max-height: 400px;
    &:hover{
        box-shadow: 0px 0px 20px 4px rgb(236, 236, 236);
        .product-item__bottom{
            &__more{
                &__cart{
                    width: 37px;
                }
            }
        }
    }
    &__top{
        width: 100%;
        min-height: 250px;
        img{
            width: 100%; 
            height: 100%;
            image-rendering: -webkit-optimize-contrast;
            object-fit: contain;
            object-position: center;
        }
    }
    &__bottom{
        display: flex; flex-direction: column;
        height: 100%;
        margin-top: 5px;
        &__name{
            font-weight: 600;
            max-height: 20px;
            overflow: hidden;
            word-break: break-word;
        }
        &__info{
            height: 55px;
            overflow-y: hidden;
            word-break: break-word;
            font-size: 14px;
            margin: 10px 0 5px;
            color: rgb(133, 133, 133);
        }
        &__more{
            margin-top: auto;
            display: flex; align-items: center; justify-content: space-between;
            &__price{
                font-weight: 600;
            }
            &__cart{
                width: 0;
                overflow: hidden;
                transition: .3s ease-in-out;
                button{ 
                    img{
                        filter: invert(1);
                    }
                }
            }
        }
    }
}

@media (max-width: 475px) {
    
    .product-item{
        max-height: 300px;
        font-size: 14px;
        padding: 10px 0;
        &:hover{
            box-shadow: unset;
        }
        &__top{
            min-height: 150px;
            img{
                aspect-ratio: 1/1;
            }
        }   
        &__bottom{
            &__name{
                max-height: 40px;
                height: 40px;
            }
            &__info{
                margin: 5px 0px 0px;
                font-size: 12px;
                height: 47px;
                padding: 0px 5px;
            }
            &__more{
                &__cart{
                    display: none;
                }
            }
        }
    }
}
</style>