<template>
    <div class="category">

        <div class="container">
            <div class="category__main">
                <div class="section-head">
                    <h2>{{category.title}}</h2>
                </div>

                <div class="category__main__grid" v-for="subcategory in category.subcategory" :key="subcategory._id">

                    <div class="section-head">
                        <h2>{{subcategory.title}}</h2>
                        <u><router-link :to="`/subcategory/${subcategory._id}`">Посмотреть всё</router-link></u>
                    </div>

                    <div class="products-grid">
                        <product-item :items="subcategory.products"/>
                    </div>

                </div>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
import productItem from '../components/product-item.vue'

export default {
    components:{
        productItem,
    },
    data(){
        return{
            category:{},
            id: this.$route.params['id'],
        }
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    watch:{
        $route(to){
            axios.get(`${this.BASIC_URL}/category/get/`+to.params.id)
            .then(response =>{
                this.category = response.data
                console.log(this.category);
            })
        }
    },
    created(){
        axios.get(`${this.BASIC_URL}/category/get/`+this.id)
        .then(response =>{
            this.category = response.data
            console.log(this.category);
        })
    },
}
</script>

<style lang="less" scoped>
.category{
    padding: 30px 0px;
    .container{
        .category__main{
            .section-head{
                font-size: 26px;
                padding: 0px 0px 20px;
                border-bottom: 1px solid #000;
            }
            &__grid{
                display: block;
                padding: 30px 0px;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                .section-head{
                    font-size: unset;
                    display: flex; align-items: center; justify-content: space-between;
                    border: 0;
                }
            }
        }
    }
}

@media(max-width:500px){
    .category .container .home__products__item .products-grid{
        grid-template-columns: repeat(2, 1fr); grid-gap: 5px;
    }
}
</style>