<template>
    <div class="subcategory">

        <div class="container">
            <div class="home__products__item">
                <div class="section-head">
                    <h2>{{subcategory.title}}</h2>
                </div>

                <div class="home__products products-grid">
                    <product-item :items="products"/>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
import productItem from '../components/product-item.vue'

export default {
    components:{
        productItem,
    },
    data(){
        return{
            subcategory:{},
            id: this.$route.params['id'],
            products:[],
        }
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    watch:{
        $route(to){
            axios.get(`${this.BASIC_URL}/subcategory/`+to.params.id)
            .then(response =>{
                this.subcategory = response.data
            })
            axios.get(`${this.BASIC_URL}/product/api/bycats/`+to.params.id)
            .then(response => {
                this.products = response.data
                console.log(this.products);
            })
            this.$store.dispatch('changeRoute', false)
        }
    },
    created(){
        axios.get(`${this.BASIC_URL}/subcategory/`+this.id)
        .then(response =>{
            this.subcategory = response.data
        })
        axios.get(`${this.BASIC_URL}/product/api/bycats/`+this.id)
        .then(response => {
            this.products = response.data
        })
    },
    mounted(){
        this.$store.dispatch('changeRoute', false)
    }
}
</script>

<style lang="less" scoped>

.subcategory{
    padding: 30px 0px;
    .container{
        .home__products__item{
            .section-head{
                padding: 0px 0px 20px;
            }
            .products-grid{
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
            }
        }
    }
}

@media(max-width:500px){
    .subcategory .container .home__products__item .products-grid{

        grid-template-columns: repeat(2, 1fr); grid-gap: 5px;
    }
}
</style>