<template>
    <div class="cart" v-bind:class="{ show: isActive }">
        <div class="cart__box" >
            <div class="cart__box__top">
                <button class="close" @click="showCart()">
                    <i class="fas fa-times fa-2x"></i>
                </button>
                <div class="title">Корзина</div>
            </div>

            <div class="cart__box__bottom" :class={checkout:cartToggle} v-show="!isInfo">
                <div class="cart-item" v-for="(item,index) in cartComputed" :key="item">
                    <button class="cart__remove" @click="remove(index)"><i class="fa fa-times"></i></button>
                    <div class="cart-item__left">
                        <div class="img">
                            <img :src="`${this.BASIC_URL}/`+item.pro.img">
                        </div>
                    </div>

                    <div class="cart-item__right">
                        <router-link :to="`/product/${item.pro._id}`"  class="title">{{item.pro.title}}</router-link>
                        <div class="price">{{item.pro.price}} сум x {{item.count}}</div>
                    </div>

                </div>
            </div>
            <div class="checkout" v-show="!isInfo">
                <input type="text" v-model="checkout.name" placeholder="Ваша имя">
                <input v-maska="'+998 (##) ###-##-##'" class="input phone" placeholder="+998 (__) __ __ __" v-model.trim="checkout.phone"/>
                <input type="text" v-model="checkout.address" placeholder="Адрес доставки">
            </div>
            <div class="cart__itog" v-show="!isInfo">
                <div class="cart__summa">
                    Итого: <span>{{summa}} сум</span>
                </div>
                <button @click="sendCheckout()" class="cart__checkout" v-show="checkout.name.length>0&&checkout.address.length>0&&checkout.phone.length>18">Оформить заказа</button>
            </div>
            <div class="cart__info" v-show="isInfo">
                Ваш заказ отправлено! Скоро вам позвонить наш менеджер для подверждение заказа!
            </div>
        </div>
    </div>
    
</template>

<script>
import axios from 'axios'
export default {
    
    data(){
        return{ 
            isInfo:false,
            checkout:{
                name:'',
                phone:'+998',
                address:'',
                product:[]
            },
            cartToggle:false,
            cart:[]
        }   
    },
    computed: {
        isActive(){
            return this.$store.getters.getCart
        },
        cartComputed(){
            
            return this.$store.getters.getCartProducts
        },
        BASIC_URL(){
            return this.$store.getters.getURL
        },
        summa(){
            let s = 0
            if (this.cartComputed){
                this.cartComputed.forEach(el => {
                    s+= el.pro.price * el.count
                })
            }
            return s.toLocaleString()
        }
    },
    methods:{
        sendCheckout(){
            let product = []
            let data = this.cartComputed
            data.forEach(item => {
                product.push({
                    id: item.pro._id,
                    count: item.count
                })
            })
            axios.post(`${this.BASIC_URL}/cart/`,{
                name: this.checkout.name,
                phone: this.checkout.phone,
                adress: this.checkout.address,
                product
            }).then(res => {
                if (res.data == 'ok'){
                    this.isInfo = true
                    this.$store.dispatch('setCartProducts',[])
                    this.$cookies.remove('cart')
                    setTimeout(()=>{
                        this.isInfo = false
                        this.checkout = {
                            name:'',
                            phone:'+998',
                            address:'',
                            product:[]
                        }
                    },3000)
                }
            })
        },
        remove(index){
            this.cartComputed.splice(index,1)
            this.$store.dispatch('setCartProducts',this.cartComputed)
            this.$cookies.set('cart',JSON.stringify(this.cartComputed))
        },
        showCart() {
            this.$store.dispatch('showCart', false)
        }
    },
    mounted(){
        if (this.$cookies.isKey('cart')){
            this.$store.dispatch('addCartProduct',JSON.parse(this.$cookies.get('cart'))) 
        }
    }
}
</script>

<style lang="less" scoped>
.cart__info {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 100%;
    height: 100%;
    text-align: center;
    font-size: 18px;
    padding: 10px;
    font-weight: bold;

}
.checkout {
    display: flex;
    flex-direction: column;
    flex-basis: 0;
    overflow: hidden;
    flex-basis: 253px;
    padding: 10px;
    input {
        display: block;
        width: calc(100% - 20px);
        padding: 11px;
        transition: 0.2s ease-in-out;
        margin-bottom: 10px;
    }
}



.cart__checkout {
    font-size: 16px;
    padding: 10px 20px;
    align-items: center;
    color: #fff;
    background-color: var(--main-color);
    margin: 0px 10px;
    text-align: center;
    display: block;
    width: 100%;
}
.cart__summa {
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    padding: 10px;
    span {
        font-weight: bold;
    }
}
.cart__remove{
    position: absolute;
    right: 5px;
    top: 5px;
    background: unset;
    opacity: 0;
    transition: 0.5s;
}
.cart{
    position: fixed;
    top: 0; right: -100%;
    width: 25%; height: 100vh;
    background-color: #fff;
    z-index: 9;
    transition: .3s ease;
    box-shadow: 0px 0px 20px 0px #46464654;
    &__itog {
        flex-basis: 10%;
    }
    &.show{
        right: 0;
    }
    &__box{
        padding: 20px 0px;
        display: flex; flex-direction: column;
        height: 100%;
        
        &__top{
            flex-basis: 5%;
            display: flex; align-items: center;
            padding: 0px 10px;
            .close{
                background: 0;
                flex-basis: 20%;
                display: flex;
            }
            .title{
                flex-basis: 80%;
                font-size: 20px;
                font-weight: 600;
            }
        }
        &__bottom{
            overflow-y: scroll;
            flex-basis: 100%;
            .cart-item{
                display: flex;
                border-bottom: 1px solid #e4e4e4;
                transition: .3s;
                position: relative;
                padding: 5px 10px;
                &:hover {
                    .cart__remove{
                        opacity: 1;
                    }
                }
                &__left{
                    flex-basis: 20%;
                    width: 20%;
                    .img{
                        display: flex;
                        img{
                            width: 100%;
                        }
                    }
                }
                &__right{
                    flex-basis: 80%;
                    width: 80%;
                    padding: 10px;
                    display: flex; flex-direction: column; justify-content: space-between;
                    .title{
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
                &:hover{
                    background-color: rgb(243, 243, 243);
                }
            }
        }
    }
}

@media (max-width: 475px){
    .cart{
        z-index: 7;
        &.show{
            width: 100%;
        }
        &__box{
            padding: 70px 30px;
        }
    }
}
</style>