<template>
    <div class="home container">

        <section class="home__slider">
            <Splide class="splide" :options=" {
                type: 'loop', 
                rewind: true, 
                cover: false, 
                autoplay: false, 
                classes: {
                    arrow : 'splide__arrow arrow',
                    pagination: 'splide__pagination pagination',
                    page: 'splide__pagination__page pagination__item' },
                breakpoints: {
                    475: {
                        arrows: false
                    },
                }
                }">
                <SplideSlide>
                    <img src="@/assets/img/slider/slider-main-1.jpg" alt="">
                </SplideSlide>
                <SplideSlide>
                    <img src="@/assets/img/slider/slider-main-2.jpg" alt="">
                </SplideSlide>
                <SplideSlide>
                    <img src="@/assets/img/slider/slider-main-3.jpg" alt="">
                </SplideSlide>
            </Splide>
        </section>

        <section class="home__products">
            <!-- <div class="loading">
                <div class="item item7">
                    <div class="dot dot1"></div>
                    <div class="dot dot2"></div>
                    <div class="dot dot3"></div>
                </div>
            </div> -->
            
            <div class="home__products__item" v-for="(subcategory, index) in subcategories" :key="index">

                <div class="section-head">
                    <h2>{{subcategory[0].title}}</h2>
                    <u><router-link :to="`/subcategory/${subcategory[0]._id}`">Посмотреть всё</router-link></u>
                </div>


                <div class="home__products__grid products-grid">
                    <product-item :items="subcategory[0].products"/>
                </div>

            </div>
            
        </section>

    </div>
</template>

<script>

import productItem from '../components/product-item.vue'
import axios from 'axios'

export default {
    components:{
        productItem,
    },
    data(){
        return{
            subcategories:[],
            bottom: false,
            count:0,
            end: false
        }
    },
    watch: {
        bottom(newValue) {
            if (newValue) {
                this.addBeer();
            }
        },
        $route(to){
            console.log(to);
            this.count = 0
            this.addBeer()
        }
    },
    created() {
        window.addEventListener("scroll", () => {
            this.bottom = this.bottomVisible();
        });
        this.addBeer();
    },
    computed: {
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    methods: {
        bottomVisible() {
            const scrollY = window.scrollY;
            const visible = document.documentElement.clientHeight;
            const pageHeight = document.documentElement.scrollHeight - 200;
            const bottomOfPage = visible + scrollY >= pageHeight;
            return bottomOfPage || pageHeight < visible;
        },
        addBeer() {
            axios.get(`${this.BASIC_URL}/subcategory/api/forhome/${this.count}`).then((response) => {
                if (response.data.length > 0 ) {
                    this.count++
                    this.subcategories.push(response.data)
                    if (this.bottomVisible()) {
                        this.addBeer();
                    }
                }
            });
        }
    },
}

</script>

<style lang="less">
.products-grid{
    display: grid; grid-template-columns: repeat(4, 1fr); grid-gap: 15px;
    width: 100%;
}
.home{
    .section-head{
        display: flex; align-items: center; justify-content: space-between;
        margin-bottom: 20px;
    }
    &__slider{
        padding: 20px 0px 30px;
        .splide{
            &__slide{
                img{
                    aspect-ratio: 16 / 9;
                    width: 100%;
                    object-fit: cover;
                }
            }
            &__track{
                border-radius: 6px;
            }
            .arrow{
                svg{
                    fill: var(--main-color);
                }
            }
            .pagination{
                bottom: -20px;
                .pagination__item{
                    border-radius: 2px;
                    height: 3px;
                    width: 25px;
                    &.is-active{
                        background-color: var(--main-color);
                        transform: unset;
                    }
                }
            }
        }
    }
    &__products{
        .loading{
            display: flex; justify-content: center;
            .item7 {
                background: transparent;
                position: relative;
                width: 70px;
                height: 50px;
                .dot {
                    width: 20px;
                    height: 20px;
                    display: block;
                    position: absolute;
                    top: 50%;
                    border-radius: 50%;
                    background-color: var(--main-color);
                    transform: translateY(-50%);
                    animation: item7-dot 1.2s ease-in-out infinite;
                    &.dot1 {
                        left: 20%;
                    }
                    &.dot2 {
                        left: 50%;
                        animation-delay: 0.2s;
                    }
                    &.dot3 {
                        left: 80%;
                        animation-delay: 0.4s;
                    }
                }
            }
        }
        @keyframes item7-dot {
            0% {
                opacity: 1;
                top: 50%;
            }
            50% {
                opacity: 0.3;
                top: 0;
            }
            100% {
                opacity: 1;
                top: 50%;
            }
        }
        &__grid{
            padding-top: 10px;
            padding-bottom: 50px;
        }
    }
}

@media (max-width: 475px) {
    .products-grid{ 
            grid-template-columns: repeat(2, 1fr); grid-gap: 5px;
        }
    .home{
        .section-head{
            margin-bottom: 10px;
            h2{
                margin: 10px 0px 0px 0px;
                font-size: 18px;
            }
            a{
                font-size: 14px;
            }
        }
    }
}
</style>